*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}
:root{
    --color-1:#B4161B;
    --color-2:#f8d3d38c;
}
body{
    width: 100%;
}
::-webkit-scrollbar{
   width: 10px;
    appearance: none;
}
::-webkit-scrollbar-thumb{
    background: var(--color-1);
}
.covid_container main{ 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 3rem 0;
    width: 100%;
}
.covid_container main h1{
    margin: 2rem 0;
    font-size: 2rem;
    font-weight: 600;
    position: relative;
    color: var(--color-1);
    text-align: center;
}
.covid_container main h1::after{
  position: absolute;
  content: '';
  width:100%;
  height: 2px;
  background: var(--color-1);
  bottom: -10px;
  left: 0;
}
.covid_container main div{
    width: 90%;
    margin: 0 auto;
}
.covid_container main .covid_table {
    margin-top: 2rem;
    padding:0  12px;
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .covid_container main .covid_table td, .covid_table th {
    border: 1px solid #ddd;
    padding: 8px;
}
  
.covid_container main .covid_table tr:nth-child(odd){
    background-color: var(--color-2);
}
  
.covid_container main .covid_table tr:hover {
    background-color: rgba(221, 221, 221, 0.37);
}
  
.covid_container main .covid_table thead th {
    padding: 12px 14px;
    text-align: left;
    background-color:var(--color-1);
    color: white;
}
.covid_container main .covid_table tbody td{
  text-align: left;
  padding: 12px 14px;
  cursor: pointer;
}

@media only screen and (max-width:768px){
    .covid_container main h1{
        width: 100%;
        font-size: 25px;
    }
    .covid_container main .covid_container main{ 
     align-items: start;
    }
    .covid_container main .covid_table {
        font-size: 12px;

    }
}